import "antd/dist/antd.css";
import "./App.less";
import { Layout } from "antd";
import { Route, Switch } from "react-router-dom";
import { GameRunner } from "./pages/game/GameRunner";
import { HomePage } from "./pages/Home";
import { ContentPart } from "./pages/parts/Content";
import { FooterPart } from "./pages/parts/Footer";
import { HeaderPart } from "./pages/parts/Header";
import { ControllerTesterPage } from "./pages/Tester";
import { ViewerPage } from "./pages/Viewer";

function App() {
  return (
    <div className="App">
      <Layout className="layout">
        <HeaderPart />
        <ContentPart>
          <Switch>
            <Route exact path="/">
              <HomePage />
            </Route>
            <Route path="/game">
              <GameRunner />
            </Route>
            <Route path="/viewer">
              <ViewerPage />
            </Route>
            <Route path="/tester">
              <ControllerTesterPage />
            </Route>
          </Switch>
        </ContentPart>
        <FooterPart />
      </Layout>
    </div>
  );
}

export default App;
